<template>
    <!-- src="../assets/bg.jpg" -->
    <img class="bgimg" :src="coverImgUrl">
    <div class="top">
        <div class="example-block">
            <!-- @change="handleChange"  :props="props" v-loading.fullscreen.lock="fullscreenLoading" -->
            <el-cascader @change="selectCity" placeholder="请选择/搜索" size="large" v-model="cityId" :options="options" />
        </div>

    </div>
    <div class="weatherShow" v-if="Object.keys(cityData).length">
        <div class="w-top">
            <div class="city-show">
                <!-- <i class="el-icon-place" /> -->
                <el-icon color="white" :size="30">
                    <Place />
                </el-icon>
                <p class="city">{{cityData.basic.city}}</p>
            </div>
            <div class="time">{{cityData.basic.update.loc}}</div>
        </div>
        <div class="w-center">
            <p class="big">{{cityData.now.cond.txt}}</p>
            <p class="small">{{cityData.now.wind.dir}}</p>
            <p class="small">{{cityData.daily_forecast[0].tmp.min}}~{{cityData.daily_forecast[0].tmp.max}}℃</p>
        </div>
        <div class="w-bottom">
            <p>未来天气预报</p>
            <div class="table">
                <el-table stripe :data="tableData" border style="width: 40%;">
                    <!-- <el-table-column prop="date" label="日期" width="180" /> -->
                    <!-- <el-table-column label="日期" width="180">
                        <template slot-scope="scope">


                        </template>
                    </el-table-column> -->
                    <el-table-column label="日期" width="180">
                        <template #default="row">
                            <el-icon><Clock /></el-icon>
                            <span style="margin-left: 10px">{{ row.row.date }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="cond.txt_n" label="天气" />
                    <el-table-column prop="tmp.min" label="最低温度(℃)" width="120" />
                    <el-table-column prop="tmp.max" label="最高温度(℃)" width="120" />
                    <el-table-column prop="wind.dir" label="风向" />
                </el-table>
            </div>

        </div>
    </div>
    <div class="tip" v-else>
        请选择城市开始查询吧
    </div>
</template>

<script>
import { ElNotification, ElLoading } from 'element-plus';
import { getWeather } from '@/request/api/home'
import { regionData, CodeToText } from 'element-china-area-data'
export default {
    data() {
        return {
            options: regionData,
            cityId: [],
            addressAll: [],
            address: '',
            tableData: [],
            cityData: {},
            coverImgUrl: require("@/assets/bg.jpg"),
            // fullscreenLoading:true,

        };
    },
    methods: {
        selectCity() {
            this.addressAll = [];
            this.cityId.forEach((city) => {
                this.addressAll.push(CodeToText[city]);
            });
            this.inquireWeather();
        },
        // async inquireWeather() {
        //     let res = await getWeather(this.addressAll[2]);
        //     console.log(res);
        // }
        async inquireWeather() {
            if (this.addressAll[2] == '市辖区' || this.addressAll.length == 2) {
                this.address = this.addressAll[1];
            } else {
                this.address = this.addressAll[2];
            }
            let res = await getWeather(this.address);
            this.tableData = res.data.daily_forecast;
            this.cityData = res.data;
            ElNotification({
                title: '成功',
                message: '查询成功',
                type: 'success',
                duration: '2000'
            })
            //背景切换
            // let weather = this.cityData.now.cond.txt;
            // this.coverImgUrl = require(`@/assets/${weather}.jpg`);
            // 不封装版
            // axios.get(`https://bird.ioliu.cn/weather?city=${this.address}`).then((res) => {
            //     console.log(res);
            //     this.tableData = res.data.daily_forecast;
            //     this.cityData = res.data;
            //     //背景切换
            //     let weather = this.cityData.now.cond.txt;
            //     this.coverImgUrl = require(`@/assets/${weather}.jpg`);
            // })
            //背景切换
            this.bgChange();
        },
        bgChange() {
            let weather = this.cityData.now.cond.txt;
            this.coverImgUrl = require(`@/assets/${weather}.jpg`);
        }


    }
};
</script>

<style lang="less" scoped>
* {
    margin: 0px;
    padding: 0px;
}

.bgimg {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    margin: 0;
}

.top {
    padding: 20px 0px 10px 0px;
    display: flex;
    justify-content: center;
}

.example-block {
    margin: 1rem;
}

.example-demonstration {
    margin: 1rem;
}

.tip {
    display: flex;
    justify-content: center;
    font-size: 50px;
    text-shadow: 2px 2px 5px rgb(59, 59, 59);
    font-weight: 800;
    color: aliceblue;


}

.city-show {
    display: flex;
    justify-content: center;
    align-items: center;

    .city {
        font-size: 25px;
        font-weight: 900;
        color: aliceblue;
        text-shadow: 2px 2px 5px rgb(59, 59, 59);
    }

}

.time {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 20px;
    font-size: 20px;
    text-shadow: 2px 2px 5px rgb(59, 59, 59);
    font-weight: 800;
    color: aliceblue;
}

.w-center {
    padding-top: 10px;

    .big {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 90px;
        font-weight: 900;
        font-style: italic;
        color: aliceblue;
        text-shadow: 5px 5px 5px #ccc;
    }

    .small {
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: 900;
        font-size: 25px;
        padding-bottom: 10px;
        text-shadow: 2px 2px 5px rgb(144, 149, 216);
    }
}

.w-bottom {
    p {
        display: flex;
        justify-content: center;
        font-size: 25px;
        font-weight: 900;
        color: aliceblue;
        text-shadow: 2px 2px 5px rgb(59, 59, 59);
        padding-bottom: 10px;
    }

    .table {
        display: flex;
        justify-content: center;
        height: 200px;


    }
}
</style>